@import url(https://fonts.googleapis.com/css2?family=Amatic+SC&family=Quicksand&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Quicksand';

}


.calc_outer {
    width: 100%;
    height: 100%;
    background: white;
    padding: 2% 2% 2% 2%;

    flex-direction: column;
    justify-content: center;
    align-items: center;
    display: flex;
    border-right: 1px solid black;
}

.calc_inputBox {

    width: 100%;
    margin: 8% 0;

}

/* remove margin on first input field  */
.calc_inputBox:nth-child(1) {


    margin: 0 0 8% 0;
}

.calc_inputBox:nth-child(5) {


    margin: 0 0 0 0;
}


::-webkit-input-placeholder {
    color: black;
    opacity: 1;
}


:-ms-input-placeholder {
    color: black;
    opacity: 1;
}


::placeholder {
    color: black;
    opacity: 1;
}

.calc_input {

    width: 100%;
    
    font-size: large;
    border: 0;
    outline: 0;
    background: transparent;
    border-bottom: 1px solid black;
    font-size: large;
    display: block;
    text-align: center;

}

.calc_output {
    width: 90%;
    height: 50px;
    margin: 8% 0 0 0;
    display: flex;
    text-align: center;
    font-size: large;
}

.calc_radio {
    margin: 2%;
    text-align: center;
}

.calc_output div:nth-child(1) {

    margin-right: auto;
}

.calc_copyButton {
    outline: none;
    background-color: transparent;
    border: none;
    font-size: large;
}

.calc_copyButton:hover {
    background-color: green;
    border-radius: 100px;
    width: 80px
}

.placeHolderOutput {
    margin: 4%;
}


/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

@media screen and (max-width:960px) {
    .calc_outer{
        height: 95vh;
        border-right: 0;
    }

    .calc_inputBox {

        width: 100%;
        margin: 4% 0;

    }

    /* remove margin on first input field  */
    .calc_inputBox:nth-child(1) {


       margin: 0% 0 4%;
    }

    .calc_inputBox:nth-child(5) {


        margin: 0 0 0 0;
    }

    /* Output box */
    .calc_output {
        width: 90%;
        height: 50px;
       margin: 0;
        display: flex;
        text-align: center;
        font-size: large;
        height: 5%;
    }

    /* shrink input box */
    .calc_input {

        width: 100%;
        height: 30px;
        font-size: large;
        border: 0;
        outline: 0;
        background: transparent;
        border-bottom: 1px solid black;
        font-size: large;
        display: block;
        text-align: center;
    
    }

}

